@font-face {
  font-family: "PTMono-Regular";
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/PTMono-Regular.woff2") format("woff2"), url("../fonts/PTMono-Regular.woff") format("woff"); }

@font-face {
  font-family: "RobotoCondensed-Bold";
  font-weight: 700;
  font-display: swap;
  src: url("../fonts/RobotoCondensed-Bold.woff2") format("woff2"), url("../fonts/RobotoCondensed-Bold.woff") format("woff"); }

@font-face {
  font-family: "RobotoCondensed-Regular";
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/RobotoCondensed-Regular.woff2") format("woff2"), url("../fonts/RobotoCondensed-Regular.woff") format("woff"); }

@font-face {
  font-family: "Roboto-Medium";
  font-weight: 500;
  font-display: swap;
  src: url("../fonts/Roboto-Medium.woff2") format("woff2"), url("../fonts/Roboto-Medium.woff") format("woff"); }

body {
  font-family: "PTMono-Regular", Arial, sans-serif;
  font-size: 16px;
  line-height: 1.2;
  font-weight: 400;
  color: #666666; }

.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px; }

html {
  font-family: sans-serif;
  text-size-adjust: 100%; }

body {
  margin: 0; }

article,
aside,
details,
figcaption,
figure,
footer,
header,
main,
menu,
nav,
section,
summary {
  display: block; }

audio,
canvas,
progress,
video {
  display: inline-block; }

audio:not([controls]) {
  display: none;
  height: 0; }

progress {
  vertical-align: baseline; }

template,
[hidden] {
  display: none; }

a {
  background-color: transparent;
  text-decoration-skip: objects; }

a:active,
a:hover {
  outline-width: 0; }

abbr[title] {
  border-bottom: none;
  text-decoration: underline;
  text-decoration: underline dotted; }

b,
strong {
  font-weight: inherit; }

b,
strong {
  font-weight: bolder; }

dfn {
  font-style: italic; }

h1 {
  font-size: 2em;
  margin: 0.67em 0; }

mark {
  background-color: #ff0;
  color: #000; }

small {
  font-size: 80%; }

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline; }

sub {
  bottom: -0.25em; }

sup {
  top: -0.5em; }

img {
  border-style: none; }

svg:not(:root) {
  overflow: hidden; }

code,
kbd,
pre,
samp {
  font-family: monospace, monospace;
  font-size: 1em; }

figure {
  margin: 1em 40px; }

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible; }

button,
input,
select,
textarea {
  font: inherit;
  margin: 0; }

optgroup {
  font-weight: bold; }

button,
input {
  overflow: visible; }

button,
select {
  text-transform: none; }

button,
html [type="button"],
[type="reset"],
[type="submit"] {
  appearance: button; }

button::-moz-focus-inner,
[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner {
  border-style: none;
  padding: 0; }

button:-moz-focusring,
[type="button"]:-moz-focusring,
[type="reset"]:-moz-focusring,
[type="submit"]:-moz-focusring {
  outline: 1px dotted ButtonText; }

fieldset {
  border: 1px solid #c0c0c0;
  margin: 0 2px;
  padding: 0.35em 0.625em 0.75em; }

legend {
  box-sizing: border-box;
  color: inherit;
  display: table;
  max-width: 100%;
  padding: 0;
  white-space: normal; }

textarea {
  overflow: auto; }

[type="checkbox"],
[type="radio"] {
  box-sizing: border-box;
  padding: 0; }

[type="number"]::-webkit-inner-spin-button,
[type="number"]::-webkit-outer-spin-button {
  height: auto; }

[type="search"] {
  appearance: textfield;
  outline-offset: -2px; }

[type="search"]::-webkit-search-cancel-button,
[type="search"]::-webkit-search-decoration {
  appearance: none; }

::-webkit-input-placeholder {
  color: inherit;
  opacity: 0.54; }

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit; }

a {
  text-decoration: none; }

img {
  max-width: 100%;
  height: auto; }

.btn {
  font-family: "RobotoCondensed-Bold", Arial, sans-serif;
  font-size: 14px;
  line-height: 17px;
  text-transform: uppercase;
  text-align: center;
  color: #00B2FF;
  border: 1px solid #00B2FF;
  min-width: 180px;
  border-radius: 5px;
  box-sizing: border-box;
  padding-top: 14px;
  padding-bottom: 8px; }

.header {
  position: absolute;
  top: 0;
  width: 100%; }
  .header__wrapper {
    margin: 0 auto;
    margin-top: 30px;
    width: 74%;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    flex-wrap: wrap; }
  .header__company-name {
    font-family: "RobotoCondensed-Bold", Arial, sans-serif;
    font-weight: 700;
    font-size: 25px;
    line-height: 30px; }
  .header__tagline {
    font-family: "Roboto-Medium", Arial, sans-serif;
    font-size: 12px;
    line-height: 14px;
    color: #788891; }
  .header__contact {
    flex-grow: 1;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    flex-wrap: wrap;
    align-content: center; }
  .header a {
    color: #ffffff; }
  .header__contact a:not(.btn) {
    position: relative;
    font-family: "PTMono-Regular", Arial, sans-serif;
    font-size: 16px;
    line-height: 18px;
    margin-left: 7.625rem;
    align-self: center; }
  .header__order-call {
    margin-left: 2.375rem;
    border-color: #ffffff; }
  .header__contact a:not(.btn)::before {
    content: "";
    position: absolute;
    left: -20px;
    top: 0; }

.header__logo {
  margin-right: auto;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  flex-wrap: wrap;
  color: #ffffff; }
  .header__logo div {
    padding-left: 16px;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    flex-wrap: wrap; }

.first-screen {
  padding-top: 100px;
  background-color: #1B2931;
  background-image: url("../img/first-screen-background-desktop@1x.png"), url("../img/circuit-board-desktop@1x.png");
  background-repeat: no-repeat;
  background-position: 50%, 80% 50%;
  color: #ffffff;
  background-size: cover, auto; }
  .first-screen__wrapper {
    margin: 0 auto;
    width: 74%; }
  .first-screen__good {
    display: inline-block;
    padding-top: 231px;
    padding-bottom: 31px;
    font-family: "RobotoCondensed-Bold", Arial, sans-serif;
    font-weight: 700;
    font-size: 80px;
    line-height: 94px;
    letter-spacing: 0.07em;
    text-transform: uppercase; }
  .first-screen__directions {
    margin: 0;
    padding-left: 94px;
    padding-bottom: 37px;
    font-size: 22px;
    line-height: 34px; }
  .first-screen__btn {
    display: inline-block;
    margin-left: 94px;
    margin-bottom: 278px;
    background-color: #00B2FF;
    padding-left: 28px;
    padding-right: 28px;
    color: #ffffff; }
  .first-screen__scroll {
    position: absolute;
    top: 785px;
    left: 13%;
    transform: rotate(-90deg);
    opacity: 0.5; }

.advantages {
  margin: 0;
  padding: 0;
  margin-top: 109px; }
  .advantages__list {
    width: 61%;
    margin: 0 auto;
    list-style: none;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    flex-wrap: wrap;
    list-style: none; }
  .advantages__card {
    width: 48%;
    padding-top: 98px;
    box-sizing: border-box;
    margin-bottom: 30px;
    font-size: 16px;
    line-height: 22px;
    padding-left: 2.75em;
    padding-right: 2.75em;
    border: 1px solid #EBEBEB;
    border-left: 3px solid #00B2FF;
    background-repeat: no-repeat;
    background-size: 35px;
    background-position: 45px 40px; }
  .advantages__card:nth-child(2n+1) {
    margin-right: auto; }
  .advantages__subtitle {
    margin: 0;
    font-family: "RobotoCondensed-Regular", Arial, sans-serif;
    color: #00B2FF;
    font-size: 24px;
    line-height: 34px; }
  .advantages__card--suppliers {
    background-image: url(../img/hired-icon.svg); }
  .advantages__card--confidentiality {
    background-image: url(../img/detective-icon.svg); }
  .advantages__card--cost {
    background-image: url(../img/tag-icon.svg); }
  .advantages__card--term {
    color: #fff;
    background-color: #00B2FF;
    background-image: url(../img/watch-icon.svg);
    border-color: transparent; }
    .advantages__card--term .advantages__subtitle {
      color: #fff; }
